body {
  margin: 0;
  padding: 0;
  font-family: "Heebo", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.HomePage {
  overflow: hidden;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas:
    "header"
    "carouselOne"
    "carouselTwo"
    "carouselThree";

  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0px;
  margin: 0px;
}

#HomePageHeader {
  grid-area: header;
}

#HomePageCarouselOne {
  grid-area: carouselOne;
  padding: 0px;
  margin: 0px;
}

#HomePageCarouselTwo {
  grid-area: carouselTwo;
  padding: 0px;
  margin: 0px;
}

#HomePageCarouselThree {
  grid-area: carouselThree;
  padding: 0px;
  margin: 0px;
}

.DepartmentSection {
  background: #f8f8f8;
}

.HeadingOne h1 {
  font-weight: 800;
  font-size: 30px;
  color: #109648;
  letter-spacing: 0;
  text-align: center;
}

.HeadingFive h5 {
  font-weight: 600;
  font-size: 14px;
  color: #181818;
  letter-spacing: 0;
}

.BodyOne p {
  font-weight: 500;
  font-size: 16px;
  color: #8a8a8a;
  letter-spacing: 0;
  text-align: center;
  line-height: 24px;
}

.DepartmentList {
  padding: 10px;
}

#LearnMore h5 {
  color: #109648;
  margin: 2px;
}

.ListCTA {
  display: grid;
  grid-auto-flow: column;
  place-items: center;
}

.ListCTA .HeadingFive {
  width: 100%;
  text-align: end;
}

.ListCTA .ImageOne {
  width: 100%;
  text-align: start;
  padding-left: 20px;
  padding-top: 5px;
}

.BodyTwo p {
  font-weight: 500;
  font-size: 14px;
  color: #1d2129;
  letter-spacing: 0;
  text-align: center;
  line-height: 19px;
}

.HeadingThree h3 {
  font-weight: 600;
  font-size: 18px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  line-height: 31px;
}


#FacilitySectionDescription {
  padding-bottom: 15px;
}

.FacilitiesSection {
  padding-top: 30px;
}

.FacilitiesList {
  padding: 15px 30px;
}

.VisionSection {
  padding: 10px 20px;
  background: #f8f8f8;
}

.CarouselOneSlider {
  z-index: 1;
  display: grid;
  grid-auto-flow: column;
  grid-gap: 10px;
  overflow: auto;

  -webkit-overflow-scrolling: touch;

  -webkit-scroll-snap-type: x mandatory;

      -ms-scroll-snap-type: x mandatory;

          scroll-snap-type: x mandatory;
}

.CarouselOneSlide {
  scroll-snap-align: center;
  width: 320px;
  padding: 0px 0px;
  color: #fff;
  font-size: 20px;
}

.CarouselOneSlide img {
  padding: 0px;
  margin: 0px;
}

.CarouselOneControls {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  margin-top: -185px;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
}

#CarouselOnePreviousArrow img {
  float: left;
}

#CarouselOneNextArrow img {
  float: right;
  padding-left: 225px;
}

.CarouselTwo {
  background: #109648;
}

.CustomOne {
  padding: 10px;
}

#CustomOne h2 {
  color: #fff;
}

.CarouselTwoSlider {
  display: grid;
