}

.CarouselThree {
}

.CarouselThreeTop {
  padding-top: 20px;
  display: grid;
  grid-auto-flow: row;
  grid-gap: 0px;
  overflow: none;
  grid-template-columns: 64% 18% 18%;
  grid-template-areas: "one two three";
